.inprogress span {
  color: #fff;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 32px;
  display: inline-block;
  animation: animate 2s linear infinite;
  width: 64px;
  height: 64px;
  text-align: center;
  line-height: 64px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  margin: 0 0px;
}

@media screen and (max-width: 850px) {
  .inprogress span {
    font-size: 25px;
    width: 50px;
    height: 50px;
    line-height: 50px;
  }
}

@keyframes animate {
  0% {
    color: rgb(0, 251, 255);
    box-shadow: 0 2px 10px rgb(0, 251, 255);
    border: 1px solid rgb(0, 251, 255);
  }
  33.3% {
    color: rgb(250, 118, 255);
    box-shadow: 0 2px 10px rgb(250, 118, 255);
    border: 1px solid rgb(250, 118, 255);
  }
  66.6% {
    color: rgb(109, 240, 255);
    box-shadow: 0 2px 10px rgb(109, 240, 255);
    border: 1px solid rgb(109, 240, 255);
  }
  100% {
    color: rgb(0, 251, 255);
    box-shadow: 0 2px 10px rgb(0, 251, 255);
    border: 1px solid rgb(0, 251, 255);
  }
}
.inprogress span:nth-child(1) {
  animation-delay: 0s;
}
.inprogress span:nth-child(2) {
  animation-delay: 0.1s;
}
.inprogress span:nth-child(3) {
  animation-delay: 0.2s;
}
.inprogress span:nth-child(4) {
  animation-delay: 0.3s;
}
.inprogress span:nth-child(5) {
  animation-delay: 0.4s;
}
.inprogress span:nth-child(6) {
  animation-delay: 0.5s;
}
.inprogress span:nth-child(7) {
  animation-delay: 0.6s;
}
.inprogress span:nth-child(8) {
  animation-delay: 0.7s;
}
.inprogress span:nth-child(9) {
  animation-delay: 0.8s;
}
.inprogress span:nth-child(10) {
  animation-delay: 0.9s;
}
.inprogress span:nth-child(11) {
  animation-delay: 1s;
}
.inprogress span:nth-child(12) {
  animation-delay: 1.1s;
}
.inprogress span:nth-child(13) {
  animation-delay: 1.2s;
}
.inprogress span:nth-child(14) {
  animation-delay: 1.3s;
}
